// 
export class Person {
    constructor (ch_name, en_name, address, address2, phone, email) {
        this.ch_name = ch_name;    // 
        this.en_name = en_name;    // 
        this.address = address;    // 
        this.address2 = address2;    // 2
        this.phone = phone;        // 
        this.email = email;        // email
    }
}

// 
export class Payment {
    constructor (branch_title, branch_code, account_name, account_code) {
        this.branch_title = branch_title;    // 
        this.branch_code = branch_code;      // 
        this.account_name = account_name;    // 
        this.account_code = account_code;    // 
    }
}

// 
export class Invoice {
    constructor (id, date, list, tax) {
        this.id = id;       //  #00
        this.date = date;   //  (yyyy/mm/dd www.)
        this.list = list;   //  (): array
        this.tax = tax;     //  (%)
    }
}

// 
export class InvoiceItem {
    constructor (des, type, hours, rate, amount, temp_des, temp_type, temp_hours, temp_rate, status_edit) {
        // 
        this.des = des;        // 
        this.type = type;      // 
        this.hours = hours;    // 
        this.rate = rate;      //  ( 300)
        this.amount = amount;  // 

        // 
        this.temp_des = temp_des;        // 
        this.temp_type = temp_type;      // 
        this.temp_hours = temp_hours;    // 
        this.temp_rate = temp_rate;      //  ( 300)

        // 
        this.status_edit = status_edit; //  boolean
    }
}